import React, { useState, useEffect } from 'react';
import useStyles from './styles';
import { TextField, Button, Typography, Paper } from '@material-ui/core';
import FileBase64 from 'react-file-base64';
import { useDispatch, useSelector } from 'react-redux';
import { createPost, updatePost } from '../../actions/posts'

const Form = ({ currentId, setCurrentId }) => {

    const classes = useStyles();
    const dispatch = useDispatch();

    const [postData, setPostData] = useState({ creator: '', title: '', message: '', tags: '', selectedFile: '' })

    const post = useSelector((state) => currentId ? state.posts.find((p) => p._id===currentId) : null);

    useEffect(()=>{
        if (post) setPostData(post);
    }, [post])

    const handleForm = (e) => {
        e.preventDefault();
        if (currentId) { 
            dispatch(updatePost(postData));  
        } else { 
            dispatch(createPost(postData)) 
        }
        clear();
    }

    const clear = () => {
        setCurrentId(null);
        setPostData({ creator: '', title: '', message: '', tags: '', selectedFile: '' });
    }


    return (
        <Paper className={classes.paper}>
            <form className={`${classes.form} ${classes.root}`} autoComplete='off' noValidate onSubmit={handleForm}>
                <Typography variant='h6' className=''>{currentId ? 'Editing' : 'Creating'} a memory</Typography>
                <TextField name='creator' variant='outlined' label='Creator' fullWidth value={postData.creator} onChange={(e)=>setPostData({...postData, creator: e.target.value })} />
                <TextField name='title' variant='outlined' label='Title' fullWidth value={postData.title} onChange={(e)=>setPostData({...postData, title: e.target.value })} />
                <TextField name='message' variant='outlined' label='Message' fullWidth value={postData.message} onChange={(e)=>setPostData({...postData, message: e.target.value })} />
                <TextField name='tags' variant='outlined' label='Tags' fullWidth value={postData.tags} onChange={(e)=>setPostData({...postData, tags: e.target.value.split(',') })} />
                <div className={classes.fileInput}>
                    <FileBase64 type='file' multiple={false} onDone={({base64})=>setPostData({ ...postData, selectedFile: base64 })} />
                </div>
                <Button className={classes.buttonSubmit} variant='contained' color='primary' size='large' type='submit' fullWidth>Submit</Button>
                <Button className={classes.buttonSubmit} variant='contained' color='secondary' size='small' onClick={clear} fullWidth>clear</Button>
            </form>
        </Paper>
    )
}

export default Form